@import url(https://fonts.googleapis.com/css2?family=Poppins&family=Roboto&display=swap);
*{
    padding: 0;
    margin: 0;
    font-family: 'Roboto', Arial, Helvetica, sans-serif;
}
header{
    padding: 10px 30px;
}
nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
nav #ulHeader{
    list-style: none;
    display: flex;
    grid-gap: 15px;
    gap: 15px;
}
.link{
    font-size: 1.1em;
    color: #1c1c1c;
    opacity: 0.7;
    transition: opacity 1s;
    cursor: pointer;
    padding-bottom: 15px;
}
.link:hover{
    opacity: 1;
    border-bottom: #348F50 2px solid;
}
#hamburger{
    display: none;
}
#headerBtn{
    padding: 12px 10px;
    border-radius: 25px;
    font-size: 1em;
    border: none;
    background-image: linear-gradient(to right, #348F50, #56B4D3);
    width: 14%;
    color: whitesmoke;
    cursor: pointer;
    transition: 0.5s;
}
button:hover{
    opacity: 0.8;
}


@media(max-width: 910px){
    #headerBtn{
        padding: 12px 10px;
        border-radius: 25px;
        font-size: 1em;
        width: 20%;
    }
}

@media(max-width: 768px){
    nav #ulHeader{
        display: none;
    }
    #headerBtn{
        display: none;
    }
    #hamburger{
        display: inline-block;
        height: 15px;
    }
    header{
        padding: 15px 50px;
    }
    nav{
        display: flex;
        justify-content: space-between;
    }
}
#container{
    display: flex;
    align-items: center;
    position: relative;
    background-color: whitesmoke;
}


/* TEXT */
#textSection{
    margin-left: 10%;
    margin-top: -10%;
}
#textSection h1{
    font-size: 4em;
    color: #1c1c1c;
    opacity: 0.8;
}
#textSection p{
    margin-top: 15px;
    font-size: 1.1em;
    opacity: 0.6;
}
#presentationBtn{
    padding: 12px 10px;
    border-radius: 25px;
    font-size: 1em;
    margin-top: 20px;
    border: none;
    background-image: linear-gradient(to right, #348F50, #56B4D3);
    width: 30%;
    color: whitesmoke;
    cursor: pointer;
    transition: 0.5s;
}

#cellphoneSection{
    background-image: url(/Easybank--React/static/media/bg-intro-desktop.8be308fd.svg);
    background-size: cover;
    width: 100%;
}
#cellphoneSection img{
    width: 90%;
}



@media(max-width: 947px){
    #textSection h1{
        font-size: 3.5em;
    }
    #textSection{
        margin-left: 10%;
        margin-top: 3%;
    }
}

@media(max-width: 768px){
    #container{
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
    }
    #cellphoneSection{
        background-image: url(/Easybank--React/static/media/bg-intro-desktop.8be308fd.svg);
        background-size: cover;   
    }
    #cellphoneSection img{
        width: 60%;
        margin-left: 20%;
    }

        /* TEXT */
    #textSection{
        margin-left: 10%;
        margin-right: 10%;
        margin-top: 50px;
        text-align: center;
    }
    #textSection h1{
        font-size: 3em;
    }
}


@media(max-width: 501px){
    #textSection h1{
        font-size: 2.5em;
    }
    #textSection p{
        font-size: 1.1em;
    }
    #presentationBtn{
        width: 45%;
    }
}

@media(max-width: 350px){
    #textSection h1{
        font-size: 2em;
    }
    #textSection p{
        font-size: 1em;
    }
    #presentationBtn{
        width: 50%;
    }
}

.container{
    background-color: whitesmoke;
    padding-bottom: 50px;
    overflow-x: hidden;
}
#textAbout{
    width: 50%;
    margin-left: 10%;
    color: #1c1c1c;
}
#textAbout h1{
    font-size: 2.5em;
    margin-bottom: 15px;
    opacity: 0.8;
}
#textAbout p {
    font-size: 1.1em;
    opacity: 0.6;
}

#cards{
    display: flex;
    margin-left: 10%;
    margin-top: 50px;
    width: 100%;
}
.card{
    width: 20%;
    color: #1c1c1c;
    padding: 0px 5px;
}
.card h4{
    font-size: 1.5em;
    padding: 15px 0px;
    opacity: 0.8;
}
.card p{
    font-size: 1.1em;
    opacity: 0.6;
}



@media(max-width: 768px){
    .container{
        background-color: whitesmoke;
        padding-bottom: 50px;
        padding-top: 100px;
    }
    #textAbout{
        width: 50%;
        margin-left: 25%;
        color: #1c1c1c;
        text-align: center;
    }
}


@media(max-width: 501px){
    #textAbout{
        width: 80%;
        margin-left: 10%;
    }
    #cards{
        display: flex;
        flex-direction: column;
        margin-left: 10%;
        width: 80%;
    }
    .card{
        width: 100%;
        text-align: center;
        padding-bottom: 40px;
    }
}

@media(max-width: 350px){
    #textAbout h1{
        font-size: 2em;
    }
    #textAbout p {
        font-size: 1em;
    }
    .card p{
        font-size: 1em;
    }
}
#containerFather{
    background-color: whitesmoke;
}
#containerArticles{
    width: 80%;
    margin-left: 10%;
    padding-top: 50px;
    padding-bottom: 100px;
}
#containerArticles h1{
    font-size: 3em;
    color: #1c1c1c;
    opacity: 0.9;
    margin-bottom: 40px;
}
#sectionArticles{
    display: flex;
    justify-content: space-between;
}
.articleBox{
    width: 23%;
    height: 390px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.articleBox h4{
    font-size: 1.05em;
    opacity: 0.9;
}
.articleBox p{
    opacity: 0.7;
}
.articleBox img{
    width: 100%;
    height: 220px;
}


@media (max-width: 768px){
    #containerArticles h1{
        text-align: center;
        font-size: 2.5em;
    }
    #sectionArticles{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .articleBox{
        width: 100%;
        grid-gap: 15px;
        gap: 15px;
        height: auto;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        padding-bottom: 50px;
    }
    .articleBox img{
        width: 45%;
    }
    .divText{
        display: flex;
        flex-direction: column;
        justify-content: space-evenly; 
    }
}


@media(max-width: 501px){
    #containerArticles{
        padding-bottom: 50px;
    }
    #sectionArticles{
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    .articleBox{
        flex-direction: column;
    }
    .articleBox img{
        width: 100%;
    }
    .divText{
        display: flex;
        width: 90%;
        margin-left: 5%;
        height: 150px;
        flex-direction: column;
        justify-content: space-evenly; 
    }
    .divText p{
        font-size: 0.9em;
    }
}

@media (max-width: 501px){
    #containerArticles h1{
        font-size: 2em;
    }
}
#footerContainer{
    width: 100%;
    height: 70px;
    padding: 30px 0px;
    background-color: #2d314e;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

/* LEFT */
#leftContainer{
    margin-left: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}
#logo{
    background-color: whitesmoke;
    padding: 5px 10px;
    border-radius: 16px;
}
#ulIcons{
    display: flex;
    grid-gap: 15px;
    gap: 15px;
    list-style: none;
}
#ulIcons li a:hover{
    border-bottom: 2px solid #31cc7e;
}


/* MIDDLE */
#middleContainer{
    width: 20%;
    height: 100%;
}
#middleContainer nav{
    display: flex;
    justify-content: space-between;
    height: 100%;
}
#middleContainer nav ul{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    list-style: none;
}
#middleContainer nav ul li a{
    text-decoration: none;
    color: whitesmoke; 
    transition: 0.5s;
}
#middleContainer nav ul li a:hover{
    color: #31cc7e;
}


/* RIGHT */
#rightContainer{
    margin-right: 10%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-end;
    height: 100%;
    color: whitesmoke;
}
#footerBnt{
    padding: 10px 0px;
    border-radius: 25px;
    font-size: 1em;
    border: none;
    background-image: linear-gradient(to right, #348F50, #56B4D3);
    width: 80%;
    color: whitesmoke;
    cursor: pointer;
    transition: 0.5s;
}
#footerBtn:hover{
    opacity: 0.8;
}

@media(max-width: 900px){
    #middleContainer{
        width: 25%;
    }

    #leftContainer{
        margin-left: 5%;
    }
    #rightContainer{
        margin-right: 5%;
    }
}

@media(max-width: 570px){
    #footerContainer{
        width: 100%;
        height: auto;
        padding: 30px 0px;
        background-color: #2d314e;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    }
    #leftContainer{
        margin-left: 0%;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        grid-gap: 15px;
        gap: 15px;
        height: auto;
    }

    #middleContainer{
        width: auto;
        height: auto;
        margin-top: 30px;
    }
    #middleContainer nav{
        flex-direction: column;
        justify-content: flex-start;
        grid-gap: 10px;
        gap: 10px;
        height: auto;
        text-align: center;
    }
    #middleContainer nav ul{
        grid-gap: 10px;
        gap: 10px;
        height: auto;
    }

    #rightContainer{
        margin-right: 0%;
        margin-top: 10px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
        height: 100px;
    }
}
